/* Style the links inside the navigation bar */
.Nav .topnav  {
  float: left;
  color: #f2f2f2;
  text-align: center;
  /* padding: 14px 16px; */
  text-decoration: none;
  font-size: 17px;
  position: -webkit-sticky;
  position: sticky;
}
/* Change the color of links on hover */
.Nav .topnav a:hover {
  background-color: #ddd;
  color: black;
}
/* Add a color to the active/current link */
.Nav .topnav a.active {
  background-color: #4CAF50;
  color: white;
}
